export default {
	data(){
		return {
			src:'',
			cameraSrc:''
		}
	},
	components:{
	},
	methods:{
		error(e){
			console.log(e);
		},
		takePhoto(){
			// 获取图片的上下文
			const ctx = uni.createCameraContext();
			ctx.takePhoto({
				success:function(e){
					console.log(e);
					this.src = e.tempImagePath;
				}
			})
		},
		getAlbum(){
			uni.chooseImage({
				complete:function(e){
					console.log(e);
				},
				success:function(e){
					console.log(typeof e.tempFilePaths);
					console.log(JSON.stringify(e.tempFilePaths));

					this.cameraSrc = JSON.stringify(e.tempFilePaths);
				},
				fail:function(err){
					console.log(err);
				}
			})
		}
	}
}
